@font-face {
  font-family: 'MICEGothic';
  src: url("../public/font/JalnanOTF00.woff"); 
  font-weight: 400;
  font-style: normal;
}

.list {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  scroll-snap-align: center;
}

.api-page-title {
  font-size: 5rem;
}


.name {
  font-size:  3rem;
}

.settings {
  min-height: 100vh;
  display: flex;
  font-family: "MICEGothic";
  justify-content: center;
  align-items: center;
  flex-direction: column;
  scroll-snap-align: center;
}


.api-page-name {
  font-size: 3rem;
}

.sub-page-title {
  font-size: 1.5rem;
}

.main-page-title2 {
  font-size: 4rem;
}

#main-page {
  background: #292b2f;
  color: #ffff;
}

#page {
  background: #292b2f;
  color: #fff;
}

.nav {
  background: #292b2f;
  width: 100%;
  display: flex;
  color: #ffff;
  padding: 20px;
  font-weight: 600;
  font-size: 20px;
}

.api-page-sub {
  font-size: 1.5rem;
}

body {
  font-family: "MICEGothic";
}

#list {
  background: #292b2f;
  color: white;
}
