@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Do+Hyeon&display=swap');
@font-face {
  font-family: 'MICEGothic';
  src: url("../public/font/JalnanOTF00.woff"); 
  font-weight: 400;
  font-style: normal;
}

.align {
  text-align: left;
}

.marin {
  margin: 0.05em;
}

.h1 {
  max-width:100%;
  height:auto;
  border-radius: 5px;
  align-items: center;
}

html {
  scrollbar-width:none;
}

img {
  max-width:100%;
  height:auto;
  border-radius: 5px;
}

#contact {
  background: #F2F2F2;
  color: #292b2f;
}

.contact-sub-title {
  font-size: 2.7rem;
}

#main {
  height: 100vh;
  overflow: auto;
  scroll-snap-type: y mandatory;
}



#main-page {
  background: #292b2f;
  color: white;
}

.h2 {
  text-align: left;
}

#cc {
  background: #F2F2F2;
  color: #292b2f;
}

.cc_name {
  font-size: 4rem;
}

.cc_sub {
  font-size: 1.8rem;
}


#skill {
  background: #292b2f;
  color: rgb(255, 255, 255);
  /* margin: 10%; */
}

/* #skill {
  background: #292b2f;
  color: rgb(255, 255, 255);
  margin: 10%;
} */

.skill-page-title {
  font-size: 5rem;
}

.skill-page-sub-title {
  font-size: 2rem;
}

.contact-page-title {
  font-size: 6rem;
}

.main-page-title {
  font-size: 6rem;
}

.setting {
  min-height: 100vh;
  display: flex;
  font-family: "MICEGothic";
  justify-content: center;
  align-items: center;
  flex-direction: column;
  scroll-snap-align: center;
}


body {
  margin: 0;
  font-family: "MICEGothic";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media screen and (max-width: 800px) {
  .main-page-title {
    font-size: 5rem;
  }
}